.org-cat-shell {
  min-height: 100vh;
  padding: 60px 24px;
  text-align: center;
  font-family: "Poppins", sans-serif;
}

/* STEPPER */

.org-stepper {
  display: flex;
  justify-content: center;
  gap: 32px;
  margin-bottom: 30px;
}

.step {
  opacity: 0.4;
  font-weight: 600;
}

.step.active {
  opacity: 1;
  color: #7f00ff;
}

/* SUBTITLE */

.subtitle {
  color: #777777;
  margin-bottom: 32px;
}

/* CATEGORY GRID */

.category-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));
  gap: 20px;
  max-width: 900px;
  margin: 0 auto 40px;
}

/* CATEGORY CARD */

.category-card {
  border: 2px solid #eeeeee;
  border-radius: 16px;
  padding: 24px 12px;
  cursor: pointer;
  transition: transform 0.25s ease, box-shadow 0.25s ease, border-color 0.25s ease;
  background: #ffffff;
}

.category-card:hover {
  transform: translateY(-6px);
  box-shadow: 0 8px 22px rgba(0, 0, 0, 0.08);
}

.category-card.active {
  border-color: #7f00ff;
  box-shadow: 0 10px 30px rgba(127, 0, 255, 0.2);
}

.category-card .icon {
  margin-bottom: 12px;
}

/* CONTINUE BUTTON */

.continue-btn {
  padding: 14px 36px;
  border-radius: 40px;
  border: none;
  background: #7f00ff;
  color: #ffffff;
  font-weight: 700;
  cursor: pointer;
  transition: background 0.2s ease, transform 0.2s ease;
}

.continue-btn:hover {
  background: #6a00d6;
  transform: translateY(-2px);
}

/* FOOTER */

.footer-text {
  margin-top: 20px;
  font-size: 14px;
}

.footer-text a {
  color: #7f00ff;
  font-weight: 600;
  text-decoration: none;
}

.footer-text a:hover {
  text-decoration: underline;
}
